<template>
    <v-card rounded="0" class="toolbar_div">
        
        <div v-if="isMobile" id="nav" class="nav_mobile">
            <div class="top_manu_mobile">
                <ToolbarMainMobile></ToolbarMainMobile>
            </div>
        </div>
        <div v-else id="nav" class="nav_pc">
            <div class="top_img_pc">
                <router-link to="/">
                    <v-img :src="require('../assets/img/logo/company_no_logo.png')" width="120" />
                </router-link>

            </div>
            <div class="top_manu_pc">
                <ToolbarMainPC style="float: left;"></ToolbarMainPC>
            </div>
        </div>
    </v-card>
</template>

<script>
import ToolbarMainMobile from '@/components/ToolbarMainMobile.vue'
import ToolbarMainPC from '@/components/ToolbarMainPC.vue'
export default {
    components: {
        ToolbarMainMobile,
        ToolbarMainPC
    },
    data() {
        return {
            showMenu: false,
            isMobile: false,
        };
    },
    mounted() {
        // Add event listener to detect window resize and check media query
        window.addEventListener("resize", this.checkMediaQuery);
        // Initial check
        this.checkMediaQuery();
    },
    beforeUnmount() {
        // Remove the event listener to prevent memory leaks
        window.removeEventListener("resize", this.checkMediaQuery);
    },
    methods: {
        checkMediaQuery() {
            // Use window.matchMedia to check if the media query matches
            const isMobile = window.matchMedia("(max-width: 768px)").matches;
            // If the value has changed, update the state
            if (isMobile !== this.isMobile) {
                this.isMobile = isMobile;
            }
        },
        toggleMenu() {
            this.showMenu = !this.showMenu;
        },
    },
}
</script>

