      gate_ball_images: [
        // Gate Ball
        require('../assets/img/in_game/gate_ball/img_001.jpg'),
        require('../assets/img/in_game/gate_ball/img_002.jpg'),
        require('../assets/img/in_game/gate_ball/img_003.jpg'),
        require('../assets/img/in_game/gate_ball/img_004.jpg'),
        require('../assets/img/in_game/gate_ball/img_005.jpg'),
        require('../assets/img/in_game/gate_ball/img_006.jpg'),
      ],
      park_golf_images: [
        // Park Golf
        require('../assets/img/in_game/park_golf/img_001.jpg'),
        require('../assets/img/in_game/park_golf/img_002.jpg'),
        require('../assets/img/in_game/park_golf/img_003.jpg'),
        require('../assets/img/in_game/park_golf/img_004.jpg'),
        require('../assets/img/in_game/park_golf/img_005.jpg'),
        require('../assets/img/in_game/park_golf/img_006.jpg'),
        require('../assets/img/in_game/park_golf/img_007.jpg'),
        require('../assets/img/in_game/park_golf/img_008.jpg'),
      ],
      screen_golf_images: [
        // Screen Golf
        require('../assets/img/in_game/screen_golf/img_001.png'),
        require('../assets/img/in_game/screen_golf/img_002.png'),
        require('../assets/img/in_game/screen_golf/img_003.png'),
        require('../assets/img/in_game/screen_golf/img_004.png'),
        require('../assets/img/in_game/screen_golf/img_005.png'),
        require('../assets/img/in_game/screen_golf/img_006.png'),
        require('../assets/img/in_game/screen_golf/img_007.png'),
        require('../assets/img/in_game/screen_golf/img_008.png'),
        require('../assets/img/in_game/screen_golf/img_009.png'),
      ],
      gateShowLargeImage: false,
      parkShowLargeImage: false,
      screenShowLargeImage: false,
      gateLargeImageIndex: 0,
      parkLargeImageIndex: 0,
      screenLargeImageIndex: 0,
      isMobile: false
    };
  },
  mounted() {
    // Add event listener to detect window resize and check media query
    window.addEventListener("resize", this.checkMediaQuery);
    // Initial check
    this.checkMediaQuery();
  },
  beforeUnmount() {
    // Remove the event listener to prevent memory leaks
    window.removeEventListener("resize", this.checkMediaQuery);
  },
  methods: {
    showImage(index, status) {
      switch (status) {
        case 'GATE':
          this.gateLargeImageIndex = index;
          this.gateShowLargeImage = true;
          break;
        case 'PARK':
          this.parkLargeImageIndex = index;
          this.parkShowLargeImage = true;
          break;
        case 'SCREEN':
          this.screemLargeImageIndex = index;
          this.screenShowLargeImage = true;
          break;
      }
      // this.largeImageIndex = index;
      // this.showLargeImage = true;
    },
    checkMediaQuery() {
      // Use window.matchMedia to check if the media query matches
      const isMobile = window.matchMedia("(max-width: 768px)").matches;
      // If the value has changed, update the state
      if (isMobile !== this.isMobile) {
        this.isMobile = isMobile;
      }
    }
  },
}
</script>

